import React, { Component } from 'react';
import "../Estilos/EstilosBase.css"
import "./Ventana1.css"
import "./presentation.css"
import PineBackground from '../../Assets/Img/pineBackground.png'
import ProfileImage from '../../Assets/Img/profilePictureCompress1.jpg'
import ImageBack from '../../Assets/Img/BackgroundHome1.jpg'
import ParticleBackground from '../Particulas/Particulas';



class Ventana1 extends Component {
    render() {
        return (

            <div className='containerVentana1'>
                <section class='one'>
                    <img src={ImageBack} alt="" className='ImgBack' />
                    <div className='containerProfile'>
                        <img src={ProfileImage} alt="" className='profileImg' />
                        <ParticleBackground/>
                        <div className='welcomeMessage'>
                            ¡Bienvenido! Mi nombre es
                        </div>
                        <div className='myNameStyle'>
                            Ignacio Diaz Orlandi
                        </div>
                        <div className='informationStyleOne'>
                            "Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."
                        </div>
                    </div>
                </section>
                <section class='two' id='dos'>
                    <h1>DOS</h1>
                    <img src={PineBackground} alt="" className='pineBackground' />
                </section>
                <section class='three' id='tres'>
                    <img src={PineBackground} alt="" className='pineBackground' />
                    <h1>TRES</h1>
                </section>
            </div>

        );
    }
}

export default Ventana1;